#lodging-description{
  margin-left: auto;
  margin-right: auto;
  max-width: 1240px;

  #container-primary-infos {
    display: flex;
    justify-content: space-between;

    #container-logement-infos {
      #active-name-location {
        font-size: 36px;
        margin-top: 30px;
        margin-bottom: 10px;
      }

      #active-place-location {
        font-size: 18px;
        margin: 0;
      }

      #active-name-location, #active-place-location {
        color: #FF6060;
      }
    }

    #container-owner-stars {
      #evaluation {
        display: flex;
        justify-content: flex-end;

        .star {
          padding: 4px;
        }
      }
      #container-owner {
        display: flex;
        justify-content: flex-end;
      }
    }

  }

  #collapses{
    display: flex;
    justify-content: space-between;
    margin: 0px 50px 50px 50px;

    .collapse-section{
      width: 40%;

      .collapse-content{
        .collapse-text{
          .list-equipments, .description-logement-collapse{
            list-style-type: none;
            margin: 0;
            padding: 40px 20px 40px 20px;

            li{
              font-size: 18px;
            }
          }

          .description-logement-collapse{
            font-size: 18px;
          }
        }
      }
    }
  }
}

@media (max-width: $brk_laptop_large) {
  #lodging-description{
    #container-primary-infos{
      justify-content: normal;
      margin-left: 50px;
      margin-right: 50px;
      #container-logement-infos{
        #active-name-location{
          font-size: 30px;
        }
      }

      #container-owner-stars{
        margin-left: auto;
      }
    }

    #collapses{

    }
  }
}

@media (max-width: $brk_laptop_small) {
  #lodging-description{
    #container-primary-infos{
      #container-logement-infos{
        #active-name-location{
        }
      }

      #container-owner-stars{

      }
    }

    #collapses{

    }
  }
}

@media (max-width: $brk_tablet_large) {
  #lodging-description{
    #container-primary-infos{
      flex-direction: column;
      #container-logement-infos{
        #active-name-location{
        }

        .tags{
          margin-bottom: 16px;
        }
      }

      #container-owner-stars{
        margin-left: 0;
        display: flex;
        align-items: center;
        margin-bottom: 16px;

        #container-owner{
          order: 1;
          margin-left: auto;

          .owner{
            margin-top: 0;
          }
        }

        #evaluation{
          justify-content: normal;
        }
      }
    }

    #collapses{
      flex-direction: column;
      align-items: center;

      #collapse-description{
        margin-bottom: 20px;
      }

      .collapse-section{
        width: 100%;
      }
    }
  }
}

@media (max-width: $brk_tablet_medium) {
  #lodging-description{
    #container-primary-infos{
      margin-left: 20px;
      margin-right: 20px;

      #container-logement-infos{
        #active-name-location{
          font-size: 18px;
        }

        #active-place-location{
          font-size: 14px;
        }

        .tags{
          .tag{
            padding: 3px 5px 3px 5px;

            span{
              font-size: 10px;
            }
          }
        }
      }

      #container-owner-stars{
        #container-owner{
          .owner{
            #owner-name{
              font-size: 12px;
            }

            img{
              width: 32px;
            }
          }
        }

        #evaluation{
        }
      }
    }

    #collapses{
      .collapse-section{
        .collapse-title{
          font-size: 13px;
        }
        svg{
          width: 15px;
        }
        .collapse-content{
          .collapse-text{
            .description-logement-collapse, .list-equipments li{
              font-size: 12px;
            }

            .description-logement-collapse, .list-equipments{
              padding: 23px 11px 23px 11px;
            }
          }
        }
      }
    }
  }
}